import React, { useEffect, useState } from "react";
import Hero from "./home/Hero";
import Featured from "./home/Featured";
import Fruits from "./home/Fruits";
import Service from "./home/Service";
import Vegetables from "./home/Vegetables";
import Banner from "./home/Banner";
import Bestseller from "./home/Bestseller";
import Fact from "./home/Fact";
import Testimonial from "./testimonial/Testimonial";
import Milk from "./home/Milk";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { Modal, Button } from "antd";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { toast, ToastContainer } from "react-toastify";

import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
import TopDiscount from "./home/TopDiscount";

const CategoryCard = ({ title, id, imageUrl, index }) => {
  const navigation = useNavigate();
  // console.log("imageUrl==>",imageUrl);

  const scrollToTop = (categoryName) => {
    navigation("/shop", { state: { categoryId: categoryName } });
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  return (
    <div className="mx-3 duration-500 rounded-lg w-60 hover:translate-y-2">
      <div
        className={`lg:mb-0 mb-3 border-2  cursor-pointer  rounded-lg relative 
      ${index % 2 === 0 ? "border-[#00b09b]" : "border-[#96c93d]"}`}
        onClick={() => scrollToTop(id)}
      >
        <img
          src={`https://majesticmall.net/image/${imageUrl}`}
          alt={title || "Image"}
          className="object-cover rounded-lg size-60"
          onError={(e) => {
            e.target.src = "../img/example.jpg"; // Replace with your default image URL
          }}
        />
        <span
          className={`h-16 absolute left-0 flex justify-center items-center bottom-0 w-full text-white ${
            index % 2 === 0 ? "bg-[#00b09b] " : "bg-[#96c93d]"
          }`}
        >
          {title}
        </span>
      </div>
    </div>
  );
};

const Home = () => {
  const [paymentShow, setPaymentShow] = useState(false);
  const textEncy = Cookies.get("AllText");
  const encryptedUser = Cookies.get("user");
  // const user = JSON.parse(CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(CryptoJS.enc.Utf8));
  // const encryptedUser = Cookies.get('user');
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const user = encryptedUser
    ? JSON.parse(
        CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;

  const AllText = textEncy
    ? JSON.parse(
        CryptoJS.AES.decrypt(textEncy, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;
  // console.log("AllText", AllText);

  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;

  const [paymentComplete, setPaymentComplete] = useState(false);
  const [showModal, setShowModal] = useState(true);

  const handlePayment = async () => {
    try {
      const clubRegisteredDate = new Date().toISOString();
      const config = {
        url: "/user/changeClubStatus/" + user._id,
        method: "post",
        baseURL: "https://majesticmall.net/api",
        hearder: { "content-type": "application/json" },
        headers: {
          Authorization: `Bearer ${token}`,
        },
        data: {
          clubRegisteredDate,
        },
      };

      const response = await axios.post(
        `https://majesticmall.net/api/user/addPaymentPhone`,
        {
          userId: user?._id,
          username: user?.name,
          Mobile: user?.mobile,
          orderId: "CLUBMember" + Math.random(0, 10),
          amount: 200,
          config: JSON.stringify(config),
          redirecturl: "/login",
          callBack: "/",
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status == 200) {
        return window.location.assign(response.data?.url?.url);
        setPaymentComplete(true);
        setPaymentShow(false);
        handleClick();
      }
    } catch (error) {
      toast.error("Unable to complete registration. Please try again.");
    }
  };
  const handleClick1 = async () => {
    setPaymentShow(true);
    setShowModal(false);
  };
  const [banner, setbanner] = useState([]);
  const getbanner = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/getallclubbanner",
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );
    if ((res.status = 200)) {
      ////console.log(res);
      setbanner(res.data?.clubbanner);
    }
  };
  useEffect(() => {
    getbanner();
  }, []);
  const handleClick = async () => {
    try {
      const clubRegisteredDate = new Date().toISOString();
      axios
        .post(
          `https://majesticmall.net/api/user/changeClubStatus/` + user._id,
          {
            clubRegisteredDate,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        )
        .then(function (response) {
          if (response.status === 200) {
            let updatedUser = { ...user, club: true, clubRegisteredDate };
            const encryptedUser = CryptoJS.AES.encrypt(
              JSON.stringify(updatedUser),
              encryptionKey
            ).toString();
            Cookies.set("user", encryptedUser, {
              expires: 7,
              secure: true,
              sameSite: "Strict",
            });

            // localStorage.setItem("user", JSON.stringify(updatedUser));
            toast.info("Registered as club member");
            window.location.reload();
          }
        })
        .catch(function (error) {
          toast.info("Not able to complete");
        });
    } catch (error) {
      console.warn(error);
      toast.info("Not able to complete");
      return error;
    }
  };

  const [categorydata, setcategorydata] = useState([]);
  const getcategory = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/getCategory",
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );
    if ((res.status = 200)) {
      ////console.log(res);
      setcategorydata(
        res.data?.category.filter((item) => item.status === "Active")
      );
    }
  };
  useEffect(() => {
    getcategory();
  }, []);
  const closeModal = () => {
    setShowModal(false);
    localStorage.setItem("clubModalSkipped", "true");
  };
  const chunkArray = (array, size) => {
    const chunkedArr = [];
    for (let i = 0; i < array.length; i += size) {
      chunkedArr.push(array.slice(i, i + size));
    }
    return chunkedArr;
  };

  const chunkedCategories = chunkArray(categorydata, 6);

  // console.log("categorydata",categorydata);

  return (
    <>
      <ToastContainer />
      <Hero />
      <Bestseller AllText={AllText} categorydata={categorydata} />
      <div className="product-section bg-[#d6edd9] my-10">
        <p className="text-[16px] uppercase text-center m-0 p-0  text-gradient font-extrabold quicksand">
          Trending Item
        </p>
        <h1
          // className="mb-6 text-2xl font-bold text-center md:text-2xl lg:text-2xl"
          className="text-2xl md:text-4xl lg:text-4xl font-bold text-center quicksand mt-1 text-[#000]"
        >
          Our Categories
        </h1>
        <div className="flex items-center justify-center p-0 m-0 mb-4">
          <span className="w-[12%] h-1 gradient1 rounded-md m-0 p-0"></span>
        </div>
        <Carousel
          showArrows={true}
          showStatus={false}
          showIndicators={false}
          infiniteLoop={true}
          autoPlay={true}
          interval={3000}
          showThumbs={false}
        >
          {chunkedCategories?.map((chunk, index) => (
            <div key={index} className="category-row justify-content-center ">
              {chunk?.map((category, categoryIndex) => (
                <CategoryCard
                  key={categoryIndex}
                  title={category.categoryName}
                  id={category._id}
                  imageUrl={category?.categoryimage}
                  index={categoryIndex}
                />
              ))}
            </div>
          ))}
        </Carousel>
      </div>

      <Fruits AllText={AllText} />

      <Banner />

      <Featured />

      <TopDiscount />

      <Testimonial />
      {!user?.club && user && (
        <Modal
          title={
            <h2 style={{ textAlign: "center", fontWeight: "bold" }}>
              Join Our Club!
            </h2>
          }
          visible={showModal}
          onCancel={closeModal}
          footer={null}
          centered
          bodyStyle={{ padding: "20px", textAlign: "center" }}
        >
          {banner.slice(0, 1).map((e) => (
            <>
              <img
                src={`https://majesticmall.net/image/${e.clubbanner}`}
                alt="Join Our Club"
                loading="lazy"
                onError={(e) => {
                  e.target.src = "../img/example.jpg"; // Replace with your default image URL
                }}
                style={{ width: "100%", height: "auto", marginBottom: "20px" }}
              />
              <p style={{ fontSize: "16px", marginBottom: "20px" }}>{e.type}</p>
            </>
          ))}
          <button className="buttons" onClick={handleClick1}>
            Join as a Club Member
          </button>
        </Modal>
      )}
      <Modal
        title="Complete Your Payment"
        visible={paymentShow}
        onCancel={() => setPaymentShow(false)}
        footer={null}
      >
        <div className="p-6">
          <div className="flex flex-col items-center">
            <div className="w-full p-4 mb-6 rounded-lg bg-gray-50">
              <p className="text-center text-gray-600">
                Please make a payment of 200 INR to complete your registration.
              </p>
              <p className="text-3xl font-bold text-center text-gray-800">
                ₹200
              </p>
            </div>

            <div className="w-full space-y-4">
              <button
                onClick={handlePayment}
                className="w-full bg-[#000] text-white py-3 rounded-lg hover:bg-opacity-90 transition-colors font-medium flex items-center justify-center"
              >
                <span className="mr-2 fas fa-lock"></span>
                Pay Securely Now
              </button>

              <div className="flex items-center justify-center space-x-2 text-gray-500">
                <span className="fas fa-shield-alt"></span>
                <p className="mt-2 text-xs">256-bit SSL Secure Payment</p>
              </div>
            </div>
          </div>
        </div>
      </Modal>
    </>
  );
};

export default Home;
