import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addToCart, removeFromCart } from "../../redux/CartSlice";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import { Badge, message } from "antd";
import NotificationBanner from "../Notification";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
import {
  addToCart1,
  addToCarts1,
  removeFromCart1,
} from "../../redux/CartSlice1";

const Fruits = ({ AllText }) => {
  const navigate = useNavigate();

  const encryptedToken = Cookies.get("token");
  const encryptedUser = Cookies.get("user");
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;

  if (!encryptedToken || !encryptedUser || !encryptionKey) {
    console.error("Missing encrypted data or encryption key");
  }
  // Decrypt the data
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;
  const user = encryptedUser
    ? JSON.parse(
        CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
          CryptoJS.enc.Utf8
        )
      )
    : null;

  // const token = sessionStorage.getItem("token");
  const [itemQuantities, setItemQuantities] = useState({});
  const [title, setTitle] = useState([]);
  const [categorydata, setCategorydata] = useState([]);
  const [data, setData] = useState([]);

  // const user = JSON.parse(sessionStorage.getItem("user"));
  const [selectedCategory, setSelectedCategory] = useState("All Products");
  const cart = useSelector((state) => state.cart);
  const cart1 = useSelector((state) => state.cart1);

  const [selectedWeight, setSelectedWeight] = useState({});

  const dispatch = useDispatch();

  const [showAllCategories, setShowAllCategories] = useState(false);

  const [notification, setNotification] = useState({
    show: false,
    message: "",
  });

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
  };

  const gettitle = async () => {
    let res = await axios.get("https://majesticmall.net/api/admin/getTitle", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    if (res.status === 200) {
      setTitle(res.data?.Title);
    }
  };

  const getcategory = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/getCategory",
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );
    if (res.status === 200) {
      setCategorydata(
        res.data?.category.filter((item) => item.status === "Active")
      );
    }
  };

  const getproducts = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/Getproductlist",
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );
    if (res.status === 200) {
      const activeProducts = res.data.ProductList.filter(
        (item) => item.status === "Active"
      );
      setData(activeProducts);

      const initialSelectedWeight = {};
      activeProducts.forEach((product) => {
        initialSelectedWeight[product._id] = !user?.club
          ? product.weight?.[0]
          : product.Clubweight?.[0];
      });
      setSelectedWeight(initialSelectedWeight);
    }
  };

  useEffect(() => {
    getcategory();
    gettitle();
    getproducts();
  }, []);

  const categoryid = categorydata?.find(
    (ele) => ele?.categoryName == "Fruits & Vegetables"
  );

  const handleQuantityChange = (item, change) => {
    setItemQuantities((prev) => ({
      ...prev,
      [item._id]: Math.max((prev[item._id] || 1) + change, 1),
    }));
  };
  const handleSubscribe = (val) => {
    if (!user) {
      toast.info("Please login as a user to subscribe.");
    } else {
      navigate(`/subscribe/${val._id}`, {
        state: { selectedWeight: selectedWeight[val._id] },
      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    }
  };

  const handleAddToCart = (item) => {
    if (item.stock - item?.sellproduct <= 0)
      return message.error("Out of stock");
    if (Object.keys(item).length == 0)
      return message.error("Product not found");
    item.weight[0].discount = item?.discount;
    item.Clubweight[0].discount = item?.clubdiscount;
    const weightToAdd =
      selectedWeight[item._id] ||
      (!user?.club ? item.weight[0] : item.Clubweight[0]);

    const quantity = itemQuantities[item._id] || 1;
    // setNotification({
    //   show: true,
    //   message: `Success: You have added ${item.productname} to your cart!`,
    // });
    message.success(
      `Success: You have added ${item?.productname} to your cart!`
    );
    if (categoryid?._id == item?.category) {
      dispatch(
        addToCart1({
          ...item,
          selectedWeight: weightToAdd,
          quantity,
        })
      );
    } else {
      dispatch(
        addToCart({
          ...item,
          selectedWeight: weightToAdd,
          quantity,
        })
      );
    }
    // setItemQuantities(prev => ({ ...prev, [item._id]: 0 }));
  };

  const handleRemoveFromCart = (item) => {
    const weightToRemove =
      selectedWeight[item._id] ||
      (!user?.club ? item.weight[0] : item.Clubweight[0]);

    if (categoryid?._id == item?.category) {
      dispatch(
        removeFromCart1({
          _id: item._id,
          selectedWeight: weightToRemove,
        })
      );
    } else {
      dispatch(
        removeFromCart({
          _id: item._id,
          selectedWeight: weightToRemove,
        })
      );
    }
    setItemQuantities((prev) => ({
      ...prev,
      [item._id]: Math.max((prev[item._id] || 0) - 1, 0),
    }));
  };

  const scrollToTop = (val) => {
    if (categoryid?._id == val?.category) {
      navigate(`/shop-detail1/${val?._id}`);
    } else {
      navigate(`/shop-detail/${val?._id}`);
    }

    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const scrollToTop1 = (categoryName) => {
    if (categoryName === "All Products") {
      navigate("/shop");
    } else {
      navigate("/shop", { state: { categoryId: categoryName } });
    }
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  const visibleCategories = showAllCategories
    ? categorydata
    : categorydata.slice(0, 4);
  const isItemInCart = (item) => {
    if (categoryid?._id == item?.category) {
      return cart1?.items1?.some((cartItem) => cartItem._id === item._id);
    } else {
      return cart?.items.some((cartItem) => cartItem._id === item._id);
    }
  };

  let showText = Cookies.get("AllTextHead");

  // console.log("selectedCategory", selectedCategory, data, AllText);
  return (
    <div className="container mt-5 ml-auto mr-auto">
      <ToastContainer />
      <NotificationBanner
        message={notification.message}
        show={notification.show}
        onClose={() => setNotification({ ...notification, show: false })}
      />
      <div>
        <p className="text-[16px] uppercase text-center m-0 p-0  text-gradient font-extrabold quicksand">
          My collection
        </p>
        {title.slice(0, 1).map((data) => (
          <h1
            key={data._id}
            // className="mb-6 text-3xl font-semibold lg:text-2xl lg:flex lg:justify-center lg:items-center"
            className="text-2xl md:text-4xl lg:text-4xl font-bold text-center quicksand mt-1 text-[#000]"
            // style={{ color: "#45595b" }}
          >
            {data.title}
          </h1>
        ))}
        <div className="flex items-center justify-center p-0 m-0 mb-4">
          <span className="w-[15%] h-1 gradient1 rounded-md m-0 p-0"></span>
        </div>
      </div>
      <div className="flex flex-col mx-4 mb-10 md:mx-20 lg:flex lg:justify-between lg:flex-row lg:mx-10 lg:gap-10 lg:items-center">
        {/* {title.slice(0, 1).map((data) => (
          <h1
            key={data._id}
            className="mb-6 text-3xl font-semibold lg:text-2xl lg:flex lg:justify-center lg:items-center"
            style={{ color: "#45595b" }}
          >
            {data.title}
          </h1>
        ))} */}
        <div className="w-full">
          <ul className="flex flex-row flex-wrap w-full">
            <li
              className={`py-2 px-3 border rounded-full w-32 text-center mx-2 my-2 lg:mx-1 hover:cursor-pointer ${
                selectedCategory === "All Products" ? "" : ""
              }`}
              style={{
                backgroundColor:
                  selectedCategory === "All Products" ? "#ffb524" : "#f4f6f8",
                color: selectedCategory === "All Products" ? "#000" : "#000000",
              }}
              onClick={() => handleCategorySelect("All Products")}
            >
              All Products
            </li>
            {visibleCategories?.map((data) => (
              <li
                key={data.categoryName}
                className={`py-2 px-3 border rounded-full w-35 text-center mx-2 my-2 lg:mx-1 hover:cursor-pointer`}
                style={{
                  backgroundColor:
                    selectedCategory === data._id ? "#ffb524" : "#f4f6f8",
                  color: selectedCategory === data._id ? "#000" : "#000000",
                }}
                onClick={() => handleCategorySelect(data._id)}
              >
                {data.categoryName}
              </li>
            ))}
          </ul>
          {categorydata.length > 4 && (
            <button
              className="px-4 py-2 mt-2 text-gray-700 bg-gray-200 rounded hover:bg-gray-300"
              onClick={() => setShowAllCategories(!showAllCategories)}
            >
              {showAllCategories ? "View Less" : "View More"}
            </button>
          )}
        </div>
        <div className="">
          <button
            onClick={() => scrollToTop1(selectedCategory)}
            className="uppercase px-7 py-2 border border-#f7ab27-400 rounded-full  mx-auto text-black text-center block w-full text-sm font-semibold duration-500 truncate "
            style={{ backgroundColor: "#ffb524" }}
          >
            View More
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5">
        {data

          .filter((data) => {
            return (
              data.category == selectedCategory ||
              selectedCategory === "All Products"
            );
          })
          .slice(0, 14)
          .map((val) => {
            const currentSelectedWeight = selectedWeight[val._id];
            const itemQuantity = itemQuantities[val._id] || 1;
            const inCart = isItemInCart(val);

            return (
              <Badge.Ribbon text={val.tag} color="#f7ab27">
                <div
                  key={val._id}
                  className="box border rounded-lg border-#f7ab27-400 mx-auto w-full  max-w-xs shadow-lg hover:shadow-xl transition-shadow duration-300 bg-[#f4f6f8] p-2"
                  style={{ height: "300px", marginBottom: "5px" }}
                >
                  <div className="relative overflow-clip">
                    <img
                      className="w-full transition-transform duration-500 ease-in-out rounded-lg shadow-lg hover:scale-105"
                      src={`https://majesticmall.net/product/${val?.Photos[0]?.file}`}
                      alt="Product"
                      loading="lazy"
                      style={{
                        height: "130px",
                        width: "350px",
                        marginTop: "10px",
                        objectFit: "contain",
                        boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
                        cursor: "pointer",
                      }}
                      onError={(e) => {
                        e.target.src = "../img/example.jpg"; // Replace with your default image URL
                      }}
                      onClick={() => scrollToTop(val)}
                    />

                    {/* <span
                  className="absolute px-1 py-1 text-white top-1 rounded-xl"
                  style={{ backgroundColor: "#ffb524", fontSize: "13px" }}
                >
                  {val.tag}
                </span> */}
                  </div>
                  <div className="text-center ">
                    <h3
                      className="mt-4 font-semibold text-l"
                      style={{ color: "#45595b", cursor: "pointer" }}
                      onClick={() => scrollToTop(val)}
                    >
                      {val.productname.slice(0, 20)}
                    </h3>

                    <select
                      name="option[504]"
                      id="input-option504"
                      className="bg-transparent form-control1 weight resp-select-box"
                      style={{
                        maxWidth: "200px",
                        fontSize: "12px",
                        border: "1px solid #88ac46",
                      }}
                      onChange={(e) => {
                        const selectedWeightData = JSON.parse(e.target.value);

                        const selectedDiscount =
                          selectedWeightData.discountType === "discount"
                            ? val.discount
                            : val.clubdiscount;
                        setSelectedWeight((prev) => ({
                          ...prev,
                          [val._id]: {
                            ...selectedWeightData,
                            discount: selectedDiscount,
                            discountType: selectedWeightData.discountType,
                          },
                        }));
                      }}
                      value={JSON.stringify(currentSelectedWeight)}
                    >
                      {!user?.club &&
                        val.weight?.map((e) => (
                          <option
                            key={`weight-${e.id}`}
                            value={JSON.stringify({
                              ...e,
                              discount: val.discount,
                              discountType: "discount",
                            })}
                            style={{ fontSize: "12px" }}
                          >
                            {e.value} {e.units} - Rs{" "}
                            {(e.price - e.price * (val.discount / 100)).toFixed(
                              2
                            )}
                          </option>
                        ))}

                      {val.Clubweight?.map((e) => (
                        <option
                          key={`clubweight-${e.id}`}
                          value={JSON.stringify({
                            ...e,
                            discount: val.clubdiscount,
                            discountType: "clubdiscount",
                          })}
                          disabled={!user || user.club === false}
                          style={{
                            color:
                              !user || user.club === false ? "#aaa" : "inherit",
                            fontSize: "12px",
                          }}
                        >
                          {e.value} {e.units} - Rs{" "}
                          {(
                            e.price -
                            e.price * (val.clubdiscount / 100)
                          ).toFixed(2)}
                          {!user?.club && " (Club Members Only)"}
                        </option>
                      ))}
                    </select>
                  </div>
                  {val.stock - val?.sellproduct <= 0 ? (
                    <div className="flex items-center justify-center">
                      <button className="px-2 py-2 my-3 text-white bg-red-700 rounded-full">
                        Out of Stock
                      </button>
                    </div>
                  ) : (
                    <div className="flex items-center justify-between">
                      <div className="flex items-center justify-between flex-grow p-2 mt-2 justify-cente">
                        {val.subscribe === "Subscription" ? (
                          <>
                            {" "}
                            {inCart ? (
                              <div className="flex items-center border rounded-full border-#f7ab27-400 p-1 sm:p-2 bg-[#f7ab27]">
                                <button
                                  onClick={() => handleAddToCart(val)}
                                  // className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                                  className="px-2 py-3 font-semibold text-white truncate rounded-full lg:text-xs sm:text-sm "

                                  // style={{
                                  //   backgroundColor: "#f7ab27",
                                  //   color: "#fff",
                                  // }}
                                >
                                  Update cart
                                </button>
                              </div>
                            ) : (
                              <button
                                onClick={() => {
                                  !AllText?.category
                                    ? val.category == categoryid?._id
                                      ? showText
                                        ? message.error(showText)
                                        : handleAddToCart(val)
                                      : handleAddToCart(val)
                                    : handleAddToCart(val);
                                }}
                                // className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                                className="lg:text-xs sm:text-sm font-semibold text-[#f7ab27] py-2 px-2 rounded-full truncate "
                                style={{
                                  backgroundColor:
                                    !AllText?.category &&
                                    val.category == categoryid?._id
                                      ? showText
                                        ? "rgb(204 225 166)"
                                        : "#000"
                                      : "#000",
                                  color:
                                    AllText?.category &&
                                    val.category == categoryid?._id
                                      ? showText && "black"
                                      : "#fff",
                                }}
                              >
                                Buy Once
                              </button>
                            )}
                          </>
                        ) : (
                          <>
                            <div className="flex items-center justify-between mr-2">
                              <button
                                className="lg:text-lg md:text-md sm:text-sm font-semibold bg-[#ffb524] text-white rounded-full size-7 flex justify-center items-center "
                                // style={{
                                //   backgroundColor: "#ffb524",
                                //   color: "#fff",
                                //   borderRadius: "50%",
                                // }}
                                onClick={() => {
                                  !AllText?.category
                                    ? val.category == categoryid?._id
                                      ? showText
                                        ? message.error(showText)
                                        : handleRemoveFromCart(val, 1)
                                      : handleRemoveFromCart(val, 1)
                                    : handleRemoveFromCart(val, 1);
                                }}
                              >
                                -
                              </button>
                              <span className="px-2 sm:px-3">
                                {itemQuantity}
                              </span>
                              <button
                                className="lg:text-lg md:text-md sm:text-sm font-semibold bg-[#ffb524] text-white rounded-full size-7 flex justify-center items-center "
                                // style={{
                                //   backgroundColor: "#ffb524",
                                //   color: "#fff",
                                //   borderRadius: "50%",
                                // }}
                                onClick={() => {
                                  !AllText?.category
                                    ? val.category == categoryid?._id
                                      ? showText
                                        ? message.error(showText)
                                        : handleQuantityChange(val, 1)
                                      : handleQuantityChange(val, 1)
                                    : handleQuantityChange(val, 1);
                                }}
                              >
                                +
                              </button>
                            </div>
                            {inCart ? (
                              <button
                                onClick={() => handleAddToCart(val)}
                                // className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                                className="lg:text-md sm:text-sm font-semibold text-white py-2 px-3 rounded-full bg-[#f7ab27] flex items-center border border-#f7ab27-400 p-1 sm:p-2"
                                // style={{
                                //   backgroundColor: "#f7ab27",
                                //   color: "#fff",
                                // }}
                              >
                                Update cart
                              </button>
                            ) : (
                              <button
                                onClick={() => {
                                  !AllText?.category &&
                                  val.category == categoryid?._id
                                    ? showText
                                      ? message.error(showText)
                                      : handleAddToCart(val)
                                    : handleAddToCart(val);
                                }}
                                // className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                                className="text-sm sm:text-sm font-semibold text-[#000] py-2 rounded-full px-2"
                                style={{
                                  backgroundColor:
                                    !AllText?.category &&
                                    val.category == categoryid?._id
                                      ? showText
                                        ? "rgb(204 225 166)"
                                        : "#000"
                                      : "#000",
                                  color:
                                    !AllText?.category &&
                                    val.category == categoryid?._id
                                      ? showText
                                        ? "#fff"
                                        : "#fff"
                                      : "#fff",
                                }}
                              >
                                Add to cart
                              </button>
                            )}
                          </>
                        )}
                      </div>
                      {val.subscribe === "Subscription" && (
                        <button
                          onClick={() => handleSubscribe(val)}
                          className="px-2 py-2 mt-2 font-semibold text-white text-md rounded-2xl"
                          style={{
                            backgroundColor: "#0c363b",
                            marginRight: "15px",
                          }}
                        >
                          Subscribe
                        </button>
                      )}
                    </div>
                  )}
                </div>
              </Badge.Ribbon>
            );
          })}
        {/* <div className="px-4">
          <button
            onClick={() => scrollToTop1(selectedCategory)}
            className="uppercase px-3 py-3 border border-#f7ab27-400 rounded-xl my-6 mx-auto text-white text-center block w-full text-md font-semibold duration-500"
            style={{ backgroundColor: "#ffb524" }}
          >
            View More
          </button>
        </div> */}
      </div>
    </div>
  );
};

export default Fruits;
