import React from "react";
import { featured } from "../../data/Data";

const Featured = () => {
  return (
    <div className="container mx-auto mt-3">
      <div className="mx-6 md:grid md:grid-cols-2 md:gap-4 lg:grid-cols-4 ">
        {featured?.map((val) => {
          return (
            <div
              className="text-center border rounded-lg my-6 md:my-0 py-2 bg-[#d6edd9] flex p-3 justify-between items-center"
              key={val.id}
            >
              <div className="relative after flex justify-center items-center  rounded-full ">
                <span
                  className="  w-16 h-16 flec  text-[#f7ab27]   flex justify-center items-center mb-4"
                  style={{ borderRadius: "50%" }}
                >
                  {val.icon}
                </span>
              </div>
              <div>
                <h5 className=" text-l">{val.name}</h5>
                <p className="pb-2 text-[16px]" style={{ color: "#747d88" }}>
                  {val.desc}
                </p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Featured;
